import React, { Dispatch, SetStateAction, use, useState } from 'react';
interface ModelGenderOptions {
    id: number;
    title: string;
    genderSelection: string[];
}

const GenderOptions: React.FC<ModelGenderOptions> = ({ id, title, genderSelection }) => {
    return (
        <>
            <div className="gender">
                <span className="gender-label">{title}</span>
                <div className="gender-options">
                    {
                        genderSelection.map((item) => <input className="border" type="button" value={item} />)
                    }
                </div>
            </div>
        </>
    )
}
export default GenderOptions;